<template>
    <div class="router">
        <div class="card">
            <div class="card-header">
                新增优惠码
            </div>
            <div class="main">
                <el-form ref="form" :model="form" label-width="120px">
                    <el-form-item label="优惠码标题" prop="title" :rules="[{ required: true, message: '优惠码标题不能为空'}]">
                        <el-input v-model="form.title"></el-input>
                    </el-form-item>
                    <el-form-item label="优惠码" prop="number" :rules="[{ required: true, message: '优惠码不能为空'}]">
                        <el-input v-model="form.number"></el-input>
                    </el-form-item>
                    <el-form-item label="优惠价格" prop="price" :rules="[{ required: true, message: '优惠价格不能为空'}]">
                        <el-input v-model="form.price"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="onSubmit('form')">立即创建</el-button>
                        <el-button>取消</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
    </div>
</template>

<script>
    import {getData} from "../../untils/js/request"
    import store from '../../store/index'
    export default {
        components: {
        },
        name: "add-coupon",
        data() {
            return {
                status: false,
                form: {
                    'title': '',
                    'number': '',
                    'price': ''
                }
            }
        },
        mounted() {
            this.form = store.state.coupon
        },
        methods: {
            async _create() {
                let data = await getData(`/coupon/update?id=${this.form.id}`, this.form, 'POST')
                if (data.error_code === 10000) {
                    this.$message({
                        message: '修改成功',
                        type: 'success'
                    });
                }
            },
            onSubmit(form) {
                this.$refs[form].validate((valid) => {
                    if (valid) {
                        this._create()
                    } else {
                        return false;
                    }
                });
            }
        }
    }
</script>

