<template>
    <div class="router">
        <div class="card">
            <div class="card-header">
                <el-breadcrumb>
                    <el-breadcrumb-item>首页</el-breadcrumb-item>
                    <el-breadcrumb-item>订单管理</el-breadcrumb-item>
                    <el-breadcrumb-item>订单列表</el-breadcrumb-item>
                </el-breadcrumb>
            </div>
            <!--搜索-->
            <div class="search">
                <el-input placeholder="请输入商品标题" v-model="title" class="input-with-select">
                    <el-button slot="append" icon="el-icon-search">搜索</el-button>
                </el-input>
            </div>
            <!--添加-->
            <div class="create">
                <span>共有数据: {{count}} 条</span>
            </div>
            <!--数据-->
            <div class="card-body text">
                <el-table
                        :data="data"
                        stripe
                        style="width: 100%">
                    <el-table-column
                            prop="id"
                            label="ID"
                            width="80">
                    </el-table-column>
                    <el-table-column
                            prop="title"
                            label="商品名">
                    </el-table-column>
                    <el-table-column
                            prop="category"
                            label="类型"
                            width="80">
                        <template slot-scope="scope">
                            <span v-if="scope.row.category=== 0" style="font-size:12px;color:#67C23A;">普通</span>
                            <span v-if="scope.row.category=== 1" style="font-size:12px;color:#f80043;">砍价</span>
                            <span v-else style="color: #2a62f8;font-size:12px;">抽奖</span>
                        </template>
                    </el-table-column>
                    <el-table-column
                            prop="a_number"
                            label="商品价格">
                    </el-table-column>
                    <el-table-column
                            prop="c_price"
                            label="商品价格">
                    </el-table-column>
                    <el-table-column
                            prop="number"
                            label="付款金额">
                    </el-table-column>
                    <el-table-column
                            prop="create_time"
                            label="日期"
                            width="150">
                    </el-table-column>
                    <el-table-column
                            label="状态"
                            width="80">
                        <template scope="scope">
                            <span v-if="scope.row.status=== 1" style="font-size:12px;color:#67C23A;">已付款</span>
                            <span v-else style="color: #F56C6C;font-size:12px;">已发布</span>
                        </template>
                    </el-table-column>
                    <el-table-column
                            label="操作"
                            width="120">
                        <template slot-scope="scope">
                            <el-button v-if="scope.row.status=== 1"
                                       style="color:#409EFF;" type="text" size="small" @click="edit(scope.row)">确认发货</el-button>
                            <el-button v-else style="color:#F56C6C;" type="text" size="small" @click="edit(scope.row)">取消发货</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </div>
        <!--分页-->
        <div class="page">
            <!--分页-->
            <div class="page">
                <el-pagination
                        background
                        page-size="20"
                        layout="prev, pager, next"
                        @current-change="change"
                        :total="total">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
    import {getData} from '../../untils/js/request'

    export default {
        name: 'index',
        data() {
            return {
                title: '',
                total: 0,
                page: 1,
                data: []
            }
        },
        mounted() {
            this._getData()
        },
        methods: {
            async _getData() {
                let data = await getData(`/order?${this.page}`, {}, 'GET')
                if (data.error_code === 10000) {
                    this.data = data.data.data
                    this.page = data.last_page
                    this.total = data.total
                }
            },
            edit(e){
                console.log(e)
            },
            change(e) {
                this.page = e
                this._getData()
            }
        }
    }
</script>

