<template>
    <div class="main">
        <div class="router">
            <div class="card">
                <div class="card-header">
                    <el-breadcrumb>
                        <el-breadcrumb-item>首页</el-breadcrumb-item>
                        <el-breadcrumb-item>设置</el-breadcrumb-item>
                        <el-breadcrumb-item>管理员设置</el-breadcrumb-item>
                    </el-breadcrumb>
                </div>
                <!--数据-->
                <div class="card-body text">
                    <el-table :data="data" style="width: 100%">
                        <el-table-column
                                prop="id"
                                label="用户ID">
                        </el-table-column>
                        <el-table-column
                                prop="name"
                                label="用户名">
                        </el-table-column>
                        <el-table-column
                                label="修改密码">
                            <template scope="scope">
                                <span style="font-size:12px;color:#f80003;cursor:pointer" title="修改密码"
                                      @click="change(scope.row.id)">*****</span>
                            </template>
                        </el-table-column>
                        <el-table-column
                                prop="create_time"
                                label="启用时间">
                        </el-table-column>
                        <el-table-column
                                label="当前状态">
                            <template scope="scope">
                                <span style="font-size:12px;color:#f80043;">已启用</span>
                            </template>
                        </el-table-column>
                    </el-table>
                </div>
            </div>
            <!--分页-->
            <div class="page">
                <el-pagination @current-page="next"
                               background
                               layout="prev, pager, next"
                               :total="page">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
    import router from '../../router/index'
    import {getData} from "../../untils/js/request";

    export default {
        name: "index",
        data() {
            return {
                password: {'new': '', 'old': ''},
                data: []
            }
        },
        created() {
            this._getData()
        },
        methods: {
            async _getData() {
                let data = await getData(`/admin/index`, '')
                if (data.error_code === 10000) {
                    this.data = data.data.data
                }
            },
            look(src) {
                this.$alert(`<img src="${src}">`, '', {
                    dangerouslyUseHTMLString: true
                });
            },
            async _password(id, text) {
                let data = await getData(`/admin/update?id=${id}`, {'password': text}, 'POST')
                if (data.error_code === 10000) {
                    localStorage.removeItem('token')
                    router.push({path: '/login', name: '登录'})
                    window.location.reload()
                }
            },
            _change(id,text){
                this.$confirm('此操作将修改密码，并退出登陆, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'error',
                    center: true
                }).then(() => {
                    this._password(id,text)
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消'
                    });
                });
            },
            change(id) {
                this.$alert(``, `修改密码`, {
                    dangerouslyUseHTMLString: true,
                    showInput:true,
                    inputPlaceholder:'请输入新密码',
                    confirmButtonText:'确认修改',
                }).then((e) => {
                    if(e.action==="confirm"){
                        this._change(id,e.value)
                    }
                });
                // this._password(id)
            },
            next(e) {
                this.page = e
                this._getData()
            }
        }

    }
</script>

