<template>
    <div class="router">
        <div class="card">
            <div class="card-header">
                <el-breadcrumb>
                    <el-breadcrumb-item>首页</el-breadcrumb-item>
                    <el-breadcrumb-item>优惠管理</el-breadcrumb-item>
                    <el-breadcrumb-item>优惠码列表</el-breadcrumb-item>
                </el-breadcrumb>
            </div>
            <!--搜索-->
            <div class="search">
                <el-input placeholder="请输入内容" v-model="title" class="input-with-select">
                    <el-button slot="append" icon="el-icon-search">搜索</el-button>
                </el-input>
            </div>
            <!--添加-->
            <div class="create">
                <el-button type="primary" icon="el-icon-edit" @click="create">添加</el-button>
                <span>共有数据: {{total}} 条</span>
            </div>
            <!--数据-->
            <div class="card-body text">
                <el-table
                        :data="data"
                        stripe
                        style="width: 100%">
                    <el-table-column
                            prop="id"
                            label="ID"
                            width="80">
                    </el-table-column>
                    <el-table-column
                            prop="title"
                            label="优惠码标题">
                    </el-table-column>
                    <el-table-column
                            prop="number"
                            label="优惠码">
                    </el-table-column>
                    <el-table-column
                            prop="price"
                            label="优惠价格">
                    </el-table-column>
                    <el-table-column
                            prop="create_time"
                            label="上架时间"
                            width="150">
                    </el-table-column>
                    <el-table-column
                            label="状态"
                            width="80">
                        <template scope="scope">
                            <span v-if="!scope.row.status" style="font-size:12px;color:#67C23A;">待发布</span>
                            <span v-else style="color: #F56C6C;font-size:12px;">已发布</span>
                        </template>
                    </el-table-column>
                    <el-table-column
                            label="操作"
                            width="120">
                        <template slot-scope="scope">
                            <el-button v-if="!scope.row.status" style="color:#67C23A;"
                                       @click="edit(scope.row.id,scope.row.status,scope.$index)" type="text"
                                       size="small">发布
                            </el-button>
                            <el-button v-else style="color:#F56C6C;"
                                       @click="edit(scope.row.id,scope.row.status,scope.$index)" type="text"
                                       size="small">下架
                            </el-button>
                            <el-button style="color:#409EFF;" type="text" size="small" @click="openEdit(scope.row)">
                                编辑
                            </el-button>
                            <el-button style="color:#F56C6C;" @click="del(scope.row.id,scope.$index)" type="text"
                                       size="small">删除
                            </el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </div>
        <!--分页-->
        <div class="page">
            <el-pagination
                    background
                    page-size="20"
                    layout="prev, pager, next"
                    @current-change="change"
                    :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import store from '../../store/index'
    import {getData} from '../../untils/js/request'

    export default {
        name: 'index',
        data() {
            return {
                title: '',
                total: 0,
                page: 1,
                tableData: []
            }
        },
        mounted() {
            this._getData()
        },
        methods: {
            create() {
                store.commit('add_tabs', {path: 'add-article', name: '添加优惠码'})
            },
            async _getData() {
                let data = await getData(`/coupon?page=${this.page}&title=${this.title}`, {}, 'GET')
                if (data.error_code === 10000) {
                    this.data = data.data.data
                    this.total = data.data.total
                }
            },
            async _del(id, key) {
                let data = await getData(`coupon/delete?id=${id}`, {}, 'delete')
                if (data.error_code === 10000) {
                    this.data.splice(key, 1)
                    this.$message({
                        type: 'success',
                        message: '删除成功!'
                    });
                }
            },
            //  删除优惠码
            del(id, key) {
                this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this._del(id, key)
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            // 修改优惠码
            async _edit(id, status, key) {
                let data = await getData(`/coupon/update?id=${id}`, {'status': status}, 'POST')
                if (data.error_code === 10000) {
                    this.data[key]['status'] = status
                    this.$message({
                        type: 'success',
                        message: '操作成功!'
                    });
                }
            },
            // 修改优惠码状态
            edit(id, status, key) {
                let text = status ? '下架' : '上架'
                this.$confirm('此操作将' + text + '该优惠码, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this._edit(id, !status, key)
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消'
                    });
                });
            },
            // 修改优惠码
            openEdit(data) {
                store.commit('edit_coupon', data)
                store.commit('add_tabs', {path: 'edit-coupon', name: '编辑优惠码'})
            },
            // 分页
            change(e) {
                this.page = e
                this._getData()
            }
        }
    }
</script>

