<template>
    <div class="main">
        <div class="router">
            <div class="card">
                <div class="card-header">
                    <el-breadcrumb>
                        <el-breadcrumb-item>首页</el-breadcrumb-item>
                        <el-breadcrumb-item>用户管理</el-breadcrumb-item>
                        <el-breadcrumb-item>用户列表</el-breadcrumb-item>
                    </el-breadcrumb>
                </div>
                <!--搜索-->
                <div class="search">
                    <el-input placeholder="请输入用户名" v-model="title" class="input-with-select">
                        <el-button slot="append" icon="el-icon-search">搜索</el-button>
                    </el-input>
                </div>
                <!--添加-->
                <div class="create">
                    <!--<el-button type="primary" icon="el-icon-edit" @click="create">添加</el-button>-->
                    <span>共有数据: {{total}} 条</span>
                </div>
                <!--数据-->
                <div class="card-body text">
                    <el-table :data="data" style="width: 100%">
                        <el-table-column
                                prop="id"
                                label="用户ID"
                                width="80">
                        </el-table-column>
                        <el-table-column
                                prop="nickname"
                                label="用户名">
                        </el-table-column>
                        <el-table-column
                                label="用户头像" width="100">
                            <template scope="scope">
                                <span @click="look(scope.row.avatar)" style="font-size:12px;color:#67C23A;">点击查看</span>
                            </template>
                        </el-table-column>
                        <el-table-column
                                prop="sex"
                                label="性别"
                                width="120">
                        </el-table-column>
                        <el-table-column
                                prop="city"
                                label="所在地"
                                width="120">
                        </el-table-column>
                        <el-table-column
                                prop="create_time"
                                label="注册时间">
                        </el-table-column>
                    </el-table>
                </div>
            </div>
            <!--分页-->
            <div class="page">
                <el-pagination
                        background
                        page-size="20"
                        layout="prev, pager, next"
                        @current-change="change"
                        :total="total">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
    import {getData} from "../../untils/js/request";

    export default {
        name: "index",
        data() {
            return {
                title: '',
                page:1,
                total: 0,
                data: []
            }
        },
        created() {
            this._getData()
        },
        methods: {
            async _getData() {
                let data= await getData(`/user/index?page=${this.page}`,'')
                if(data.error_code===10000){
                    this.data=data.data.data
                    this.total=data.data.total
                }
            },
            look(src){
                this.$alert(`<img src="${src}">`, '', {
                    dangerouslyUseHTMLString: true
                });
            },
            change(e) {
                this.page = e
                this._getData()
            }
        }

    }
</script>

