<template>
    <div class="main">
        <div class="header">
            <!-- 头部区域 -->
            <ul class="nav layout-left">
                <li class="nav-item">
                <span title="侧边伸缩">
                    <i class="el-icon-menu"></i>
                </span>
                </li>
                <li class="nav-item">
                <span title="刷新" @click="refresh">
                    <i class="el-icon-refresh"></i>
                </span>
                </li>
            </ul>
            <ul class="nav layout-right">

                <li class="nav-item">
                <span>
                    <i class="el-icon-bell"></i>
                    <!-- 如果有新消息，则显示小圆点 -->
                    <span class="badge-dot"></span>
                </span>
                </li>
                <li class="nav-item">
                <span>
                    <i class="icon el-icon-rank"></i>
                </span>
                </li>
                <li class="nav-item" @mouseenter="out = true" @mouseleave="out = false">
                    <span>admin</span>
                    <el-collapse-transition>
                        <dl class="nav-child" v-show="out">
                            <span>基本资料</span>
                            <span>修改密码</span>
                            <span>退出</span>
                        </dl>
                    </el-collapse-transition>
                </li>
            </ul>
        </div>
        <el-tabs v-model="active" @tab-remove="removeTab" @tab-click="onTabClick">
            <el-tab-pane v-for="(item,index) in tabs" :key="index"
                         :path="item.path"
                         :title="item.name"
                         :label="item.name"
                         :name="item.path" :lazy="true" :closable="index===0?false:false"
            >
            </el-tab-pane>
        </el-tabs>
        <keep-alive>
            <router-view></router-view>
        </keep-alive>
    </div>
</template>

<script>
    import store from '../store/index'
    import router from '../router/index'

    export default {
        name: 'headers',
        data() {
            return {
                out: false,
                defaultTab: store.state.active,
                tabs: store.state.path
            }
        },
        computed: {
            active: {
                get() {
                    return store.state.active
                },
                set(value) {
                    this.defaultTab = value
                }
            }
        },
        mounted() {
            // 页面刷新打开当前路由tab
            if (this.$route.path !== '/') {
                store.commit('add_tabs', {path: this.$route.path, name: this.$route.name})
            }
        },
        methods: {
            // 打开标签
            onTabClick(tab) {
                store.commit('add_tabs', {path: tab.$attrs.path, name: tab.$attrs.title})
            },
            // 刷新
            refresh(){
              window.location.reload()
            },
            // 关闭标签
            removeTab(targetName) {
                let tabs = this.tabs
                router.push({
                    path: '/',
                    name: '主页'
                })
                this.defaultTab = '/'
                this.tabs = tabs.filter(tab => tab.name !== targetName)
            }
        }
    }
</script>

