import Vue from 'vue'
import VueRouter from 'vue-router'
import login from '../components/login'
import index from '../components/console/index'
import goods from '../components/goods/index'
import addGoods from '../components/goods/add'
import editGoods from '../components/goods/read'
import coupon from '../components/coupon/index'
import addCoupon from '../components/coupon/add'
import editCoupon from '../components/coupon/read'
import order from '../components/order/index'
import user from '../components/user/index'
import Admin from '../components/admin/index'

Vue.use(VueRouter)

const router = new VueRouter({
    routes: [
        {
            path: '/login',
            name: '登录',
            component: login
        },
        {
            path: '/',
            name: '主页',
            component: index
        },
        {
            path: '/goods',
            name: '商品列表',
            component: goods
        },
        {
            path: '/add-goods',
            name: '添加商品',
            component: addGoods
        },
        {
            path: '/edit-goods',
            name: '编辑商品',
            component: editGoods
        },
        {
            path: '/coupon',
            name: '优惠码列表',
            component: coupon
        },
        {
            path: '/add-coupon',
            name: '添加优惠码',
            component: addCoupon
        },
        {
            path: '/edit-coupon',
            name: '编辑优惠码',
            component: editCoupon
        },
        {
            path: '/order',
            name: '订单列表',
            component: order
        },
        {
            path: '/user',
            name: '用户列表',
            component: user
        },
        {
            path: '/admin',
            name: '管理员设置',
            component: Admin
        }
    ]
})
router.beforeEach(function (to, from, next) {
    let token = localStorage.getItem('token')
    //未登录
    if (!token) {
        if (to.path !== '/login') {
            return next({path: '/login', name: '登录'})
        }
        next()
    }
    // 已登录的情况再去登录页，跳转至首页
    if (to.path === '/login') {
        if (token) {
            return next({path: '/', name: '主页'})
        } else {
            return next({path: '/login', name: '登录',})
        }
    }
    next()
})
export default router