<template>
    <div class="router">
        <div class="card">
            <div class="card-header">
                新增商品
            </div>
            <div class="main">
                <el-form ref="form" :model="form" label-width="80px">
                    <el-form-item label="商品标题" prop="title" :rules="[{ required: true, message: '商品标题不能为空'}]">
                        <el-input v-model="form.title"></el-input>
                    </el-form-item>
                    <el-form-item label="选择分类" prop="category" :rules="[{ required: true, message: '分类不能为空'}]">
                        <el-select v-model="form.category" placeholder="请选择商品分类">
                            <el-option label="普通商品" value="0"></el-option>
                            <el-option label="砍价商品" value="1"></el-option>
                            <el-option label="抽奖商品" value="2"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="缩略图" prop="thumb" :rules="[{ required: true, message: '缩略图不能为空'}]">
                        <el-upload
                                class="upload-demo"
                                :action="action"
                                multiple
                                :on-success="thumbSuccess"
                                :limit="1"
                                list-type="picture">
                            <el-button size="small" type="primary">点击上传</el-button>
                            <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过2M</div>
                        </el-upload>
                    </el-form-item>
                    <el-form-item label="首页大图" prop="thumb" :rules="[{ required: true, message: '首页大图不能为空'}]">
                        <el-upload
                                class="upload-demo"
                                :action="action"
                                multiple
                                :on-success="imageSuccess"
                                :limit="1"
                                list-type="picture">
                            <el-button size="small" type="primary">点击上传</el-button>
                            <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过2M</div>
                        </el-upload>
                    </el-form-item>
                    <el-form-item label="详情页大图" prop="detail_img" :rules="[{ required: true, message: '详情页大图不能为空'}]">
                        <el-upload
                                class="upload-demo"
                                :action="action"
                                multiple
                                :on-success="detailSuccess"
                                :limit="1"
                                list-type="picture">
                            <el-button size="small" type="primary">点击上传</el-button>
                            <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过2M</div>
                        </el-upload>
                    </el-form-item>
                    <el-form-item label="视频">
                        <el-upload
                                class="upload-demo"
                                :action="action"
                                multiple
                                :on-success="videoSuccess"
                                :limit="1"
                                list-type="picture">
                            <el-button size="small" type="primary">点击上传</el-button>
                            <div slot="tip" class="el-upload__tip">只能上传mp4文件，且不超过10M</div>
                        </el-upload>
                    </el-form-item>
                    <el-form-item label="商品规格">
                        <div class="card">
                            <el-table :data="attribute" style="width: 100%">
                                <el-table-column label="数量">
                                    <template slot-scope="scope">
                                        <input v-model="scope.row.number" class="input" placeholder="请输入数量"/>

                                    </template>
                                </el-table-column>
                                <el-table-column prop="m_price" label="市场价格">
                                    <template slot-scope="scope">
                                        <input v-model="scope.row.m_price" class="input" placeholder="请输入市场价格"/>
                                    </template>
                                </el-table-column>
                                <el-table-column label="当前价格">
                                    <template slot-scope="scope">
                                        <input v-model="scope.row.c_price" class="input" placeholder="请输入当前价格"/>
                                    </template>
                                </el-table-column>
                                <el-table-column label="内部价格">
                                    <template slot-scope="scope">
                                        <input v-model="scope.row.p_price" class="input" placeholder="请输入内部价格"/>
                                    </template>
                                </el-table-column>
                                <el-table-column label="商品库存">
                                    <template slot-scope="scope">
                                        <input v-model="scope.row.stock" class="input" placeholder="请输入商品库存"/>
                                    </template>
                                </el-table-column>
                                <el-table-column label="删除">
                                    <template slot-scope="scope">
                                        <el-button type="danger" icon="el-icon-delete"
                                                   @click="del_attribute(scope.$index)"></el-button>
                                    </template>
                                </el-table-column>
                            </el-table>
                            <div class="add">
                                <el-button class="add-button" type="primary" icon="el-icon-circle-plus-outline"
                                           @click="add_attribute">增加规格
                                </el-button>
                            </div>
                        </div>
                    </el-form-item>
                    <el-form-item label="商品内容">
                        <quill-editor :options="editorOption" v-model="form.content"></quill-editor>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="onSubmit('form')">立即创建</el-button>
                        <el-button>取消</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
    </div>
</template>

<script>
    import {getData} from "../../untils/js/request"
    import 'quill/dist/quill.core.css'
    import 'quill/dist/quill.snow.css'
    import 'quill/dist/quill.bubble.css'
    import {quillEditor, Quill} from 'vue-quill-editor'
    import {container, ImageExtend, QuillWatch} from 'quill-image-extend-module'

    Quill.register('modules/ImageExtend', ImageExtend)

    export default {
        components: {
            quillEditor
        },
        name: "add-article",
        data() {
            return {
                status: false,
                action: this.Base + '/admin/tools/upload',
                form: {
                    'category': '请选择类型',
                    'title': '',
                    'thumb': '',
                    'image': '',
                    'detail_img': '',
                    'video': '',
                    'content': ''
                },
                attribute: [{'number': '', 'm_price': '', 'c_price': '', 'p_price': '', 'stock': 999}],
                editorOption: {
                    modules: {
                        ImageExtend: {
                            loading: true,
                            name: 'file',
                            action: this.Base + '/admin/tools/upload',
                            response: (res) => {
                                return this.Base + res.url
                            }
                        },
                        toolbar: {
                            container: container,
                            handlers: {
                                'image': function () {
                                    QuillWatch.emit(this.quill.id)
                                }
                            }
                        }
                    }
                }
            }
        },
        mounted() {
        },
        methods: {
            async _create() {
                let data = await getData('/goods/save/', {'form': this.form, 'attribute': this.attribute}, 'POST')
                if (data.error_code === 10000) {

                }
            },
            add_attribute() {
                this.attribute.push({'number': '', 'm_price': '', 'c_price': '', 'p_price': '', 'stock': 999})
            },
            _isNull(text) {
                if (!text || text === null || text === '' || typeof (text) === 'undefined' || !text.trim().length || text === 'null') {
                    return true
                }
            },
            // 缩略图 上传
            thumbSuccess(res, file) {
                this.form.thumb = file.response.url
            },
            // banner 上传
            imageSuccess(res, file) {
                this.form.image = file.response.url
            },
            // 详情大图 上传
            detailSuccess(res, file) {
                this.form.detail_img = file.response.url
            },
            // 视频 上传
            videoSuccess(res, file) {
                this.form.video = file.response.url
            },
            // 删除
            del_attribute(index) {
                this.attribute.splice(index)
            },
            onSubmit(form) {
                this.attribute.map((item, key) => {
                    if (this._isNull(item.number)) {
                        this.$message.error('商品规格——数量不能为空');
                        return false
                    } else if (this._isNull(item.m_price)) {
                        this.$message.error('商品规格——市场价格不能为空');
                        return false
                    } else if (this._isNull(item.c_price)) {
                        this.$message.error('商品规格——当前价格不能为空');
                        return false
                    } else if (this._isNull(item.p_price)) {
                        this.$message.error('商品规格——内部价格不能为空');
                        return false
                    } else if (this._isNull(item.m_price)) {
                        this.$message.error('商品规格——商品库存不能为空');
                        return false
                    }
                    if (this.attribute.length === key + 1) {
                        this.status = true
                    }
                })
                if (this.status === false) {
                    return false
                }
                this.$refs[form].validate((valid) => {
                    if (valid) {
                        if (this.form.category === "请选择类型") {
                            this.form.category = 0
                        } else {
                            this.form.category = parseInt(this.form.category)
                        }
                        this._create()
                    } else {
                        return false;
                    }
                });
            }
        }
    }
</script>

<style scoped>
    .main{
        margin-top:20px;
        padding:0px 30px;
        }

    .add{
        width:100%;
